$breakpoint-tablet: 1024px;

.mainpg-container {
  #profile-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 20rem;
    font-size: 1.5rem;

    .description {
      max-width: fit-content;
    }

    // tablet display
    @media (max-width: $breakpoint-tablet) {
      font-size: 2rem;
      margin: 3rem;

      #profile-container {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 1rem;
        .description {
          max-width: fit-content;
        }
      }
    }
  }
}

// tablet display
@media (max-width: $breakpoint-tablet) {
  .prjt-img {
    height: 500px;
    width: 500px;
  }
}
